.home{

}

.home .home_contact{
    width: 100%;
    display: flex;
    justify-content: center;
}

.home_contact .contactOne{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
}

.home .home_contact .contactTwo{
    flex: 1;
    display: flex;
    flex-direction: column;
    padding: 40px;
}

.home .home_contact .contactTwo h3{
    text-align: center;
    font-size: 30px;
    font-weight: 600;
    margin-bottom: 20px;
}

.home .home_contact .contactTwo .card{
    display: flex;
    align-items: center;
    padding: 5px 30px;
    gap: 20px;
    margin-bottom: 30px;
    border-bottom: 1px solid gray;
}

.home .home_contact .contactTwo .card p{
    flex-wrap: wrap;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

